import React, {FC, SyntheticEvent, useState} from "react";
import './styles/styles.scss';

export type CountInputProps<T> = {
    COUNT: number;
    setState?: (T) => void;
};

export const CountInput: FC<CountInputProps<number>> = ({
                                                            COUNT = 1,
                                                            setState
                                                        }) => {
    const [count, setCount] = useState<number | undefined>(COUNT);

    const onChangeHandler = (e:SyntheticEvent) => {
        const {value} = e.target as HTMLInputElement;
        setCount(Number(value));
        setState(Number(value));
    }

    const onClickHandler = (count: number) => {
        if (!count) return;
        setCount(count);
        setState(count);
    };

    return (
        <div className="countInput">
            <button className="countInput__Button"
                    onClick={() => onClickHandler(count - 1)}
            >
                -
            </button>
            <input
                min={1}
                max={20}
                value={count}
                onChange={onChangeHandler}
                className="countInput__InputNumber"
            />
            <button className="countInput__Button"
                    onClick={() => onClickHandler(count + 1)}
            >
                +
            </button>
        </div>
    );
}