import React, {FC} from "react";
import './styles/styles.scss';

import {useDispatch} from "react-redux";
import {Col, Row} from "antd";
import {setModalActivity} from "../../../store/slice/cart";
import {Button, Radio} from "antd";

export type RadioGroupItemProps = {
    NAME: string;
    PERIOD?: string;
    PRICE?: number;
    ADDRESS?: string;
    WORK_TIME?: string;
    pickButton?: boolean;
};

export const RadioGroupItem: FC<RadioGroupItemProps> = ({NAME, PERIOD, PRICE, ADDRESS, WORK_TIME, pickButton}) => {
    const dispatch = useDispatch();
    return (
        <Radio className="radioGroupItem" value={NAME}>
            <div className="radioGroupItem__Content">
                <Row className="row">
                    <Col span={11}>
                        <div className="radioGroupItem__Name">
                            <span>
                                {NAME}
                            </span>
                        </div>
                    </Col>
                    <Col span={5}>
                    {
                        ADDRESS
                        &&
                            <span>
                                {ADDRESS}
                            </span>
                    }
                    </Col>
                    <Col span={8}>
                        <div className="radioGroupItem__Name">
                            {
                                PERIOD &&
                                <span className="period">{PERIOD}</span>
                            }
                            {
                                PRICE && <span className="price">
                                            {PRICE}
                                        </span>
                            }
                            {
                                WORK_TIME &&
                                <span>{WORK_TIME}</span>
                            }
                        </div>
                    </Col>
                    <Col span={24}>
                        {
                            pickButton &&
                            <Button className="radioGroupItem__PickUp"
                                ghost
                                onClick={() => dispatch(setModalActivity(true))}
                            >
                                
                            </Button>
                        }
                    </Col>
                </Row>
            </div>

        </Radio>
    );
}