import React, {FC, useState} from "react";
import './styles/styles.scss';

// Components
import {Form, Radio, Row, Space, Col, Checkbox, Button} from 'antd';
import {CustomInput} from "../../../../../CustomInput/CustomInput";
import {CustomCheckbox} from "../../../../../CustomCheckbox/CustomCheckbox";
import {DeliveryRadioGroup} from "../../../../../DeliveryRadioGroup/DeliveryRadioGroup";

// Redux
import {useSelector, useDispatch} from "react-redux";

import {DeliveryList} from "../../../../../../mock/delivery";
import {changeDeliveryFormConfig, CourierForm} from "../../../../../../store/slice/cart";

// Mock


export type DeliveryFormProps = {
    items: FormItemTypes[]
};

export type FormItemTypes = {
    span: number,
    placeholder: string,
    type?: string,
    NAME: string,
    message?: string,
    required?: boolean
}

export const DeliveryForm: FC<DeliveryFormProps> = ({
                                                        items
                                                    }) => {
    const [checkedForm, setCheckedForm] = useState<boolean>(false);
    const [hideForm, setHideForm] = useState<string | undefined>(null);
    const dispatch = useDispatch();

    const onFinishHandler = (values: CourierForm) => {
        delete values[`agreement`];
        dispatch(changeDeliveryFormConfig(values));
    }

    return (
        <div className="deliveryForm">
            <Form onFinish={onFinishHandler}>
                <Row gutter={[13, 13]}>
                    <Col span={24}>
                        <DeliveryRadioGroup radioGroupList={DeliveryList} setHideForm={setHideForm}/>
                    </Col>
                    {
                        !hideForm && items.map((item) => (
                            <Col span={item.span}>
                                <Form.Item name={item.NAME}
                                           rules={[
                                               {required: item.required, message: item.message}
                                           ]}
                                >
                                    <CustomInput
                                        placeholder={item.placeholder}
                                        type={item.type}
                                        name={item.NAME}

                                    />
                                </Form.Item>
                            </Col>
                        ))
                    }
                    <Col span={24}>
                        <Form.Item name="agreement"
                                   valuePropName="checked"
                                   rules={[
                                       {required: true, message: ' '}
                                   ]}
                        >
                            <CustomCheckbox>
                                      .
                            </CustomCheckbox>
                        </Form.Item>
                    </Col>
                    <Col span={24}>
                        <Form.Item>
                            <Button
                                type="primary"
                                htmlType="submit"
                                shape="round"
                                className="deliveryForm__Submit"
                            ></Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </div>
    );
}


