import React, {FC, useState} from "react";
import './styles/styles.scss';

// Components
import {YMaps, Map, Placemark, GeoObjectProps, PlacemarkGeometry} from 'react-yandex-maps';
import {YandexMap__Shadow, YandexMapLoad__Circle} from "./YandexMap.styles";

export type YandexMapProps = {
    items: YandexMapPlacemarkProps[];
};

export type YandexMapPlacemarkProps = React.ComponentType<GeoObjectProps<PlacemarkGeometry>> & {

}

export const YandexMap: FC<YandexMapProps> = ({ items }) => {
    const [isLoad, setIsLoad] = useState<boolean | undefined>(null);

    return (
        <div className="yandexMap">
            {
                !isLoad && <YandexMapLoad />
            }
            {
                isLoad && <div className="yandexMap__Shadow" />
            }
            <YMaps>
                <Map defaultState={{center: [55.75, 37.57], zoom: 9, controls: ['zoomControl', 'fullscreenControl'],}}
                     modules={['control.ZoomControl', 'control.FullscreenControl']}
                     width={'100%'}
                     height={'100%'}
                     onLoad={() => setIsLoad(true)}
                >
                    {
                        items && items.map((item) =>
                            <Placemark {...item} />
                        )
                    }
                </Map>
            </YMaps>
        </div>
    );
};

export const YandexMapLoad = ({}) => (
    <div className="yandexMapLoad">
        <div className="yandexMapLoad__Circle" />
        <div className="yandexMapLoad__Circle" />
        <div className="yandexMapLoad__Circle" />
    </div>
)
