import React, {FC} from "react";
import './styles/styles.scss';

// Components
import {CatalogItemProps, CatalogItem} from "../CatalogItem/CatalogItem";

import {Row, Col} from "antd";
import {idGenerator} from "../../lib/idGenerator";

export type BuyWithListProps = {
    items: CatalogItemProps[];
};

export const BuyWithList: FC<BuyWithListProps> = ({items}) => (
    <section className="buyWithList">
        <h2 className="buyWithList__Title">
               
        </h2>
        <div className="buyWithList__Flex">
            {
                items.map((item, index) => (
                    <CatalogItem {...item}  />
                ))
            }
        </div>
    </section>
);