import React, {FC} from "react";
import './styles/styles.scss';
import {Form, Input, Button, Checkbox} from 'antd';
import {CustomInput} from "../../../../../CustomInput/CustomInput";
import {useSelector, useDispatch} from "react-redux";
import {changeEmailFormConfig, EmailConfig} from "../../../../../../store/slice/cart";


export type EmailFormProps = {};

export const EmailForm: FC<EmailFormProps> = ({}) => {
    const [form] = Form.useForm();
    const dispatch = useDispatch();

    const onFinishHandler = (values: EmailConfig) => {
        dispatch(changeEmailFormConfig(values));
    }

    const onValuesChangeHandler = (values) => {

    }

    return (
        <div className="emailForm">
            <Form className="form"
                  form={form}
                  onFinish={onFinishHandler}
                  onValuesChange={onValuesChangeHandler}
            >
                <Form.Item
                    name="email"
                    rules={[
                        {required: true, message: 'Please input your email!'},
                        {type: 'email'}]
                    }
                >
                    <CustomInput className="emailForm__Input" placeholder=" Email" name="email"/>
                </Form.Item>
                <div className="emailForm__InputTooltip">
                    <span>        .</span>
                </div >

                <Form.Item>
                    <Button
                        type="primary"
                        htmlType="submit"
                        shape="round"
                        className="emailForm__Submit"
                    ></Button>
                </Form.Item>

            </Form>
            <div className="emailForm__SubmitTooltip">
                <span>    TLS     </span>
            </div>
        </div>
    );
}