import React, {FC, useEffect, useState} from "react";
import './styles/styles.scss';

import {CartList} from "./CartList/CartList";

// Redux
import {useSelector} from "react-redux";

import {RootState} from "../../../../store/store";
import {priceFormater} from "../../../../lib/priceFormater";

export type CartBlockProps = {};

export const CartBlock: FC<CartBlockProps> = ({}) => {
    const {items, totalPrice} = useSelector((state: RootState) => state.cart);

    return (
        <section className="cartBlock">
            <h2 className="cartBlock__Title">
                
            </h2>
            <CartList items={items} />
            <div className="cartBlock__TotalPanel">
                    <span>
                        
                    </span>
                <span>
                        {priceFormater.format(totalPrice)}
                    </span>
            </div>
        </section>
    );
}