import React, {FC, useState} from "react";
import './styles/styles.scss';
import SuccessIcon from "/src/images/svg/success.svg";

// Components
import {Row, Col} from "antd";
import {BuyWithList} from "../BuyWithList/BuyWithList";
import {OrderBlock} from "./components/OrderBlock/OrderBlock";
import {CartBlock} from "./components/CartBlock/CartBlock";
import {PromoBlock} from "./components/CartBlock/PromoBlock/PromoBlock";

// Mocks
import {useDispatch, useSelector} from "react-redux";
import {CustomModal} from "../CustomModal/CustomModal";
import {PickUpModalContent} from "./components/OrderBlock/components/PickUpModalContent/PickUpModalContent";
import {buyWithList} from "../../mock/buyWithList";
import {RootState} from "../../store/store";
import {setModalActivity} from "../../store/slice/cart";

export type CartProps = {};

export const Cart: FC<CartProps> = ({}) => {
    const {items, openDeliveryModal} = useSelector((state: RootState) => state.cart);
    const dispatch = useDispatch();
    return (
        <div className="cart">
            {
                items && items.length ?
                <Row gutter={50}>
                    <Col span={10}>
                        <CartBlock />
                        <PromoBlock />
                    </Col>
                    <Col span={14}>
                        <OrderBlock />
                    </Col>
                    <Col span={24} className="buyWithList">
                        <BuyWithList items={buyWithList}/>
                    </Col>
                </Row>
                    : <span>,   </span>
             }
            <CustomModal
                title="Basic Modal"
                visible={openDeliveryModal}
                onCancel={() => dispatch(setModalActivity(false))}
                onOk={() => dispatch(setModalActivity(false))}
            >
                <PickUpModalContent />
            </CustomModal>

          {/* SUCCESS*/}
          <CustomModal
            title="Basic Modal"
            visible={false}
            className="SuccessModal"
          >
            <div className="successContent">
              <div className="successContent__Icon">
                <SuccessIcon width={48} height={48}/>
              </div>
              <h4 className="successContent__Title">
                        !
              </h4>
              <div className="successContent__Subtitle">
                <span>
                           ,      
                </span>
              </div>
            </div>
          </CustomModal>
        </div>
    );
}
