import React, { FC, ReactElement } from "react"
import "./styles/styles.scss"

// Components
import { Collapse, Form } from "antd"
import { EmailForm } from "./components/EmailForm/EmailForm"
import { PayForm, PayFormProps } from "./components/PayForm/PayForm"
import { DeliveryForm, DeliveryFormProps } from "./components/DeliveryForm/DeliveryForm"

// Redux
import { useSelector } from "react-redux"

// Mocks
import { orderPayItems, FormConfig } from "../../../../mock/orderBlock"
import { RootState } from "../../../../store/store"
import { OrderBlock__Title } from "./OrderBlock.styles"

const { Panel } = Collapse

export type OrderBlockProps = {};

export const OrderBlock: FC<OrderBlockProps> = ({}) => {
  const { emailConfig, deliveryConfig } = useSelector((state: RootState) => state.cart.formConfig)
  console.log(emailConfig)

  const EmailHeader: ReactElement = <div className="panelHeader">
    {emailConfig.email ? <span>: <span className="emailHeader">{emailConfig.email}</span></span> : <span></span>}
    {emailConfig.email ? <span className="changeHeader"></span> : ``}
  </div>;

  const OrderHeader: ReactElement = <div>
     
    {deliveryConfig.formData.CITY ? <span className="cityHeader">{deliveryConfig.formData.CITY}</span> : ``}
  </div>;

  return (
    <div className="orderBlock">
      <h2 className="orderBlock__Title">
         
      </h2>
      <Form.Provider>
        <Collapse accordion>
          <Panel
            key="1"
            header={EmailHeader}
            showArrow={false}
          >
            <EmailForm />
          </Panel>
          <Panel
            key="2"
            header={OrderHeader}
            showArrow={false}
            collapsible={emailConfig.email ? "header" : "disabled"}
          >
            <DeliveryForm items={FormConfig} />
          </Panel>
          <Panel
            key="3"
            header=""
            showArrow={false}
            // collapsible={deliveryConfig.formData.COUNTRY ? "header" : "disabled"}
            collapsible={"header"}
          >
            <PayForm items={orderPayItems} />
          </Panel>
        </Collapse>
      </Form.Provider>
    </div>
  )
}