import React, {FC} from "react";
import './styles/styles.scss';
import {RadioGroupItem, RadioGroupItemProps} from "../RadioGroupItem/RadioGroupItem";
import {Form, Radio} from "antd";

export type RadioGroupBlockProps = {
    items: RadioGroupItemProps[],
    name: string,
    pickButton?: boolean;
};

export const RadioGroupBlock: FC<RadioGroupBlockProps> = ({items, pickButton}) => {
    const [value, setValue] = React.useState(1);

    const onChangeHandler = e => {
        setValue(e.target.value);
    };

    return (
        <div className="radioGroupBlock">
            <Form.Item name="COURIER"
                       rules={[
                           {required: true, message: ' '}]
                       }
            >
                <Radio.Group onChange={onChangeHandler} value={value} name="COURIER">
                    {
                        items && items.map((item) => (
                            <RadioGroupItem {...item} pickButton={pickButton}/>
                        ))
                    }
                </Radio.Group>
            </Form.Item>
        </div>
    );
}