import React, {FC} from "react";
import './styles/styles.scss';

// Components
import { Form, Radio, Space, Button } from 'antd';

// Redux
import {changePayFormConfig, PayConfig} from "../../../../../../store/slice/cart";
import {useDispatch} from "react-redux";

export type PayFormProps = {
    items: PayFormItem[]
};

export type PayFormItem = {
    VALUE: string;
    TITLE: string;
}

export const PayForm: FC<PayFormProps> = ({
    items
}) => {
    const dispatch = useDispatch();

    const onFinishHandler = (values: PayConfig) => {

        dispatch(changePayFormConfig(values));
    }

    return (
        <div className="payForm">
            <Form
                onFinish={onFinishHandler}
            >
                <Form.Item name="PAY_TYPE"
                    rules={[{
                        required: true, message: `  `
                    }]}
                >
                    <Radio.Group className="group" name="PAY_TYPE">
                        <Space direction="vertical">
                            {
                                items.map(({VALUE, TITLE}) =>
                                    <Radio value={VALUE} key={VALUE} className="item">{TITLE}</Radio>
                                )
                            }
                        </Space>
                    </Radio.Group>
                </Form.Item>
                <Form.Item>
                    <Button
                        type="primary"
                        htmlType="submit"
                        shape="round"
                        className="payForm__Submit"
                    >  </Button>
                </Form.Item>
            </Form>
        </div>
    );
}