import React, {FC} from "react";
import './styles/styles.scss';
import {CartItem, CartItemProps} from "../CartItem/CartItem";

export type CartListProps = {
    items: CartItemProps[];
}

export const CartList: FC<CartListProps> = ({ items }) => (
    <div className="cartList">
        {
            items.map((item) => (
                <CartItem {...item} />
            ))
        }
    </div>
);