import React, {FC} from "react";
import './styles/styles.scss';
import {RadioGroupItem, RadioGroupItemProps} from "../../../../../../DeliveryRadioGroup/RadioGroupItem/RadioGroupItem";
import { Radio } from "antd";

export type PickUpListProps = {
    items: RadioGroupItemProps[],
};

export const PickUpList: FC<PickUpListProps> = ({items}) => (
    <div className="pickUpList">
        <Radio.Group>
            {
                items.map((item) => (
                    <RadioGroupItem {...item} />
                ))
            }
        </Radio.Group>
    </div>
);