import {CatalogItemProps} from "../../../../CatalogItem/CatalogItem";
import React, {FC, useState} from "react";
import './styles/styles.scss';

import {CountInput} from "../../../../CountInput/CountInput";

// @ts-ignore
import ArrowIcon from "/src/images/svg/close.png";
import {useDispatch} from "react-redux";
import {changeCount, removeFromCart} from "../../../../../store/slice/cart";
import {priceFormater} from "../../../../../lib/priceFormater";


export type CartItemProps = Pick<CatalogItemProps, "ID" | "IMAGE" | "NAME" | "PRICE" > & {
    COUNT: number;
}

export const CartItem: FC<CartItemProps> = ({
                                                ID,
                                                IMAGE,
                                                NAME,
                                                PRICE,
                                                COUNT
                                            }) => {
    const dispatch = useDispatch();

    const onClickHandler = (id: string) => {
        dispatch(removeFromCart(id));
    }

    const onClickCountHandler = (count: number) => {
        const payload = {
          id: ID,
            count
        };
        dispatch(changeCount(payload));
    }

    return (
        <div className="cartItem">
            <div className="cartItem__ImageWrap">
                <img decoding="async" loading="lazy" src={IMAGE}/>
            </div>
            <h5 className="cartItem__Name">
                {NAME}
            </h5>
            <CountInput COUNT={COUNT} setState={onClickCountHandler}/>
            <div className="cartItem__Price">
                <span>
                    {priceFormater.format(PRICE * COUNT)}
                </span>
            </div>
            <button className="cartItem__RemoveProduct" onClick={() => onClickHandler(ID)}>
                <img decoding="async" loading="lazy" src={ArrowIcon} height={20} width={20}/>
            </button>
        </div>
    );
}