import React, {FC, useState} from "react";
import './styles/styles.scss';

// Components
import {Tabs, TabsProps} from "../../../../../Tabs/Tabs";
import {PickUpList} from "./PickUpList/PickUpList";
import {Col, Row} from "antd";
import {YandexMap} from "../../../../../YandexMap/YandexMap";
import {pickUpModalItems} from "../../../../../../mock/pickUpModalContent";
import {mapList} from "../../../../../../mock/mapList";
import {PickUpModalContent__TopPanel} from "./PickUpModalContent.styles";

export type PickUpModalProps = {
    items?: any[];
};

export const PickUpModalContent: FC<PickUpModalProps> = ({items = []}) => {
    const [activeType, setActiveType] = useState(`list`);
    const tabList:TabsProps<string>[`items`] = [{
         value: `list`,
         title: ``
        },
        {
            value: `map`,
            title: ` `
        }
    ];

    return (
        <div className="pickUpModalContent">
            <Tabs items={tabList} onClickHandler={setActiveType} activeValue={activeType}/>

            {
                activeType === `list` && <>
                    <PickUpModalTopPanel />
                    <PickUpList items={pickUpModalItems} />
                </>
            }
            {
                activeType === `map` && <YandexMap items={mapList} />
            }
        </div>
    );
};

export const PickUpModalTopPanel = ({}) => (
    <div className="pickUpModalContent__TopPanel">
        <Row>
            <Col span={11} className="name">
              <span>
                  
              </span>
            </Col>
            <Col span={5} className="address">
              <span>
                  
              </span>
            </Col>
            <Col span={8} className="time">
              <span>
                   
              </span>
            </Col>
        </Row>
    </div>
);